/* ======== 4.1 ACCORDION ======== */
.card.card-accordion {
  border: 0;
  margin: 15px 0;
}

.card.card-accordion .card-header {
  padding: 0px;
  border-radius: 0px;
  border: none;
}

.card.card-accordion .card-header h6 {
  position: relative;
}

.card.card-accordion .card-header h6 a:after {
  top: 50%;
  -moz-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.card.card-accordion .card-header a {
  display: block;
  padding: 15px 27px 15px 20px;
  line-height: 24px;
  transition: unset;
  background: #968ff4;
  background: -webkit-linear-gradient(legacy-direction(135deg), #968ff4 45%, #6ab6e5 70%);
  background: linear-gradient(135deg, #968ff4 45%, #6ab6e5 70%);
  color: #ffffff;
}

.card.card-accordion .card-header a[data-toggle=collapse]:after {
  font-family: FontAwesome;
  font-style: normal;
  speak: none;
  display: inline-block;
  content: '\f106';
  float: right;
  line-height: 1;
  width: 22px;
  font-size: 20px;
  text-align: center;
  position: absolute;
  right: 15px;
}

.card.card-accordion .card-header a.collapsed {
  line-height: 24px;
  color: #1d2025;
  background: #ffffff;
  background: -webkit-linear-gradient(legacy-direction(180deg), #ffffff, #ffffff);
  background: linear-gradient(180deg, #ffffff, #ffffff);
  border: 0;
}

.card.card-accordion .card-header a[data-toggle=collapse].collapsed:after {
  font-family: FontAwesome;
  font-style: normal;
  speak: none;
  display: inline-block;
  content: '\f107';
}

.card.card-accordion .card-block {
  padding: 30px 20px;
  font-size: 14px;
  line-height: 26px;
  border-radius: 0;
}

/* ======== 4.2 BATTON ======== */
.btn {
  border: none;
  border-radius: 2px;
  height: 52px;
  padding: 0 25px;
  font-size: 12px;
  line-height: 52px;
  font-weight: 500;
  text-transform: uppercase;
  cursor: pointer;
  color: #ffffff;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}

@media (min-width: 992px) {
  .btn {
    font-size: 14px;
  }
}

.btn.focus, .btn:focus, .btn.active, .btn:active, .btn:hover {
  box-shadow: none;
}

.btn.btn-rounded {
  border-radius: 40px;
}

.btn i {
  font-size: inherit;
}

.btn-primary {
  background-color: #968FF4;
  border: 1px solid #968FF4;
  color: #ffffff;
  text-transform: uppercase;
  font-size: 14px;
  border-radius: 2px;
}

.btn-primary:hover {
  border: 1px solid #968FF4;
  color: #968FF4;
  background-color: transparent;
}

.btn-primary:active, .btn-primary:focus {
  box-shadow: none !important;
  background-color: transparent !important;
  color: #968FF4;
  border-color: #968FF4;
}

.btn-icon {
  height: 77px;
  border: 1px solid rgba(255, 255, 255, 0.4);
  display: inline-flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  padding: 20px;
  border-radius: 5px;
  margin-top: 30px;
  position: relative;
  z-index: 1;
}

.btn-icon:hover {
  border: 1px solid transparent;
  box-shadow: 0px 7px 15px 0px rgba(0, 0, 0, 0.1);
  background-color: #ffffff;
}

.btn-icon:hover .icon .brand-icon-svg {
  fill: #968FF4;
}

.btn-icon:hover .text h6,
.btn-icon:hover .text span {
  color: #968FF4;
}

.btn-icon .icon {
  padding-right: 20px;
}

.btn-icon .icon .brand-icon-svg {
  fill: #ffffff;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}

.btn-icon .text {
  padding-right: 20px;
  text-align: left;
}

.btn-icon .text h6 {
  color: #ffffff;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}

.btn-icon .text span {
  display: block;
  font-weight: 500;
  color: #ffffff;
  font-size: 20px;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}

.brand-icon-svg {
  fill: #ffffff;
  fill-rule: evenodd;
}

.btn-transparent {
  border: 1px solid #ffffff;
  position: relative;
  z-index: 1;
  font-size: 14px;
}

.btn-transparent:hover {
  border: 1px solid transparent;
  box-shadow: 0px 7px 15px 0px rgba(0, 0, 0, 0.1);
  color: #968FF4;
  background-color: #ffffff;
}

.scroll-btn-aria {
  position: relative;
}

.scroll-btn-aria .btn-scroll-down {
  position: absolute;
  top: -80px;
  left: 50%;
  transform: translateX(-50%);
  width: 26px;
  height: 66px;
  line-height: 60px;
  display: block;
  border: 2px solid rgba(233, 233, 233, 0.5);
  border-radius: 30px;
  z-index: 100;
  text-align: center;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

@media (min-width: 768px) {
  .scroll-btn-aria .btn-scroll-down {
    top: -110px;
    width: 28px;
    height: 68px;
    line-height: 90px;
  }
}

.scroll-btn-aria .btn-scroll-down i {
  color: #ffffff;
  display: inline-block;
  font-size: 16px;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  position: relative;
}

@media (min-width: 768px) {
  .scroll-btn-aria .btn-scroll-down i {
    font-size: 24px;
  }
}

.scroll-btn-aria .btn-scroll-down:hover i {
  animation: fadeInDown 2s infinite;
}

.btn-social {
  width: 45px;
  height: 45px;
  text-align: center;
  padding: 0;
  border-radius: 2px;
  margin: 0 auto;
  line-height: 48px;
  display: inline-block;
  background-color: rgba(106, 200, 208, 0);
  border: 1px solid #e5e5e5;
  transition: all .5s ease-in-out;
  position: relative;
  z-index: 1;
}

.btn-social i {
  margin-right: 0px;
  color: #929292;
  transition: all .5s ease-in-out;
  font-size: 18px;
}

.btn-social:hover {
  background-color: #968FF4;
  border: 1px solid #968FF4;
}

.btn-social:hover i {
  color: #ffffff;
}

/* ======== 4.3 SECTION TITLE ======== */
[class*="heading-"] {
  position: relative;
}

h3.section-title {
  position: relative;
  font-size: 26px;
  line-height: 26px;
  font-weight: 500;
  padding-bottom: 15px;
}

h3.section-title:after {
  position: absolute;
  left: 0;
  bottom: 5px;
  background: #968ff4;
  background: -webkit-linear-gradient(legacy-direction(135deg), #968ff4 45%, #6ab6e5 70%);
  background: linear-gradient(135deg, #968ff4 45%, #6ab6e5 70%);
  content: "";
  width: 100px;
  height: 1px;
}

h3.section-title.text-white:after {
  background: #ffffff;
  background: -webkit-linear-gradient(legacy-direction(180deg), #ffffff, #ffffff);
  background: linear-gradient(180deg, #ffffff, #ffffff);
}

h3.section-title-center {
  position: relative;
  font-size: 26px;
  line-height: 26px;
  font-weight: 500;
  padding-bottom: 15px;
}

h3.section-title-center:after {
  position: absolute;
  left: 50%;
  bottom: 5px;
  background: #968ff4;
  background: -webkit-linear-gradient(legacy-direction(135deg), #968ff4 45%, #6ab6e5 70%);
  background: linear-gradient(135deg, #968ff4 45%, #6ab6e5 70%);
  content: "";
  width: 100px;
  height: 1px;
  transform: translateX(-50%);
}

.section-padding {
  padding-top: 70px;
  padding-bottom: 70px;
}

@media (min-width: 992px) {
  .section-padding {
    padding-top: 100px;
    padding-bottom: 100px;
  }
}

.section_top-padding {
