/* ======== 4.4 CARD ======== */
.card.team {
  border: 0;
  padding: 13px;
}

.card.team .card-img {
  position: relative;
}

.card.team .card-img img {
  border-radius: 50%;
  width: 100%;
}

.card.team .card-img .card-img-overlay {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  background: rgba(150, 143, 244, 0.85);
  background: -webkit-linear-gradient(legacy-direction(135deg), rgba(150, 143, 244, 0.85) 45%, rgba(106, 182, 229, 0.85) 70%);
  background: linear-gradient(135deg, rgba(150, 143, 244, 0.85) 45%, rgba(106, 182, 229, 0.85) 70%);
  border-radius: 50%;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=$value)";
  filter: alpha(opacity=0);
  -moz-opacity: 0;
  -khtml-opacity: 0;
  opacity: 0;
  visibility: hidden;
  transform: scale(0);
  transition: all .5s ease-in-out;
}

.card.team .card-img .card-img-overlay .social-link {
  margin: 0;
}

.card.team .card-img .card-img-overlay .social-link .btn-social {
  width: 40px;
  height: 40px;
  line-height: 43px;
}

.card.team .card-img .card-img-overlay .social-link .btn-social i {
  color: #ffffff;
  font-size: 14px;
}

.card.team .card-img .card-img-overlay .social-link .btn-social::after {
  background-image: none;
}

.card.team .card-img .card-img-overlay .social-link .btn-social:hover {
  background-color: #ffffff !important;
  border: 1px solid #ffffff;
}

.card.team .card-img .card-img-overlay .social-link .btn-social:hover i {
  color: #968FF4;
}

.card.team .card-body a {
  display: block;
  text-align: center;
  padding: 10px;
}

.card.team .card-body a h5 {
  font-size: 20px;
}

.card.team .card-body span {
  display: block;
  color: #929292;
  text-align: center;
  text-transform: capitalize;
}

.card.team:hover .card-img .card-img-overlay {
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=$value)";
  filter: alpha(opacity=1);
  -moz-opacity: 1;
  -khtml-opacity: 1;
  opacity: 1;
  visibility: visible;
  transform: scale(1);
}

.card.team:hover .card-img .card-img-overlay .social-link .btn-social {
  background-color: transparent;
}

