/* Font Weight */
.light {
	font-weight: 300;
}
.normal {
	font-weight: 400;
}
.mid {
	font-weight: 500;
}
.mid-bold {
	font-weight: 600;
}
.bold {
	font-weight: 700;
}

/* Common class shared */
body.boxed {
	margin:0 auto;
	position: relative;
	box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.1);
    @include desktops {
		max-width: 970px;
	}
	@include large-desktops {
		max-width: 1260px;
	}
	.main-wrapper {
		background: $white-color;
	}
}


/* Color */
.text-primary {
	color: $primary-color!important;
}
.text-white {
	color: $white-color!important;
}
.text-dark {
	color: $title-color!important;
}
.light-gray {
	color: $text-color2;
}
.white-color {
	color: $white-color;
}

/* Background Color */
.bg-white {
	background-color: $white-color;
}
.bg-dark {
	background-color: $black-color;
}
.bg-water {
	background-color: transparent;
}
.bg-sand {
	background-color: $sand;
}

.bg-primary {
	background-color: $primary-color!important;
}
.bg-whiteSmoke {
	background-color: $white-smoke;
}

/*  BG GRADIENT */

.bg-gradient {
	@include linear-gradient(135deg, rgba($primary-color,1) 45%,rgba($secondary-color,1) 70%);

}
/* Overflow Hidden */
.overflow {
	overflow: hidden;
}
